import React, {Component} from 'react';
import '../../css/dashboard.css';
import {timeStamp2String} from "../util";
import {OverlayTrigger, Tooltip} from "react-bootstrap";
import * as deviceDataStatus from './DeviceDataStatus'
import Divider from '@material-ui/core/Divider';
import Link from '@material-ui/core/Link';

class DeviceDataTableXsBody extends Component {
    renderRows() {
        if (!this.props.data) {
            return null;
        }

        return this.props.data.map((device) => {
            return (
                <div key={device.id} className="m-2">
                    <div className="row">
                        <Link href="#" onClick={this.props.handleShow} data-id={device.id}>
                            {device.deviceId}
                        </Link>
                        <div className="col-xs-2 ml-auto">{this.renderStatus(device)}</div>
                    </div>
                    <div className="row">
                        <h6>{timeStamp2String(device.timestamp)}</h6>
                    </div>
                    <Divider />
                </div>
            )

        })
    }
    renderStatus(device) {
        const statusObj = deviceDataStatus[device.status];
        const statusCode = statusObj.statusCode;

        if(statusCode!=="Error"){
           return <div className={statusObj.statusClass}> </div>
        }
        return (
            <OverlayTrigger key={device.id} placement={'right'} overlay={
                <Tooltip id={`tooltip-${'right'}`}>
                    {statusObj.description}
                </Tooltip>
            }>
                <div className={statusObj.statusClass}> </div>
            </OverlayTrigger>
        )
    }

    render() {
        return (
            <div>
                {this.renderRows()}
            </div>
        );
    }
}

export default DeviceDataTableXsBody;