import React, {Component} from 'react';
import Datetime from 'react-datetime';
import '../../css/react-datetime.css';
import moment from "moment";
import {Alert, Dropdown, DropdownButton, Modal} from "react-bootstrap";

class DeviceDataTableHeader extends Component {
    state = {
        fromTimestamp: this.props.options.fromTimestamp,// || moment(moment().valueOf() - 43200000),
        toTimestamp: this.props.options.toTimestamp,// || moment(),
        deviceId: this.props.options.deviceId,
        pageSize: this.props.options.pageSize,
        errorShow: false
    }
    onStartDateTimeChange = (value) => {
        let options = {...this.state, fromTimestamp: value}
        this.setState({fromTimestamp: value})
        this.props.onChange(options);

    }
    onEndDateTimeChange = (value) => {
        let options = {...this.state, toTimestamp: value}
        this.setState({toTimestamp: value});
        this.props.onChange(options);

    }

    onSearchClick = () => {
        const timediff = this.state.toTimestamp - this.state.fromTimestamp;
        if (timediff < 0) {
            this.showError()

        }
        this.props.onSearchClick(this.state);
    }
    closeError = () => this.setErrorShow(false);
    showError = () => this.setErrorShow(true);
    setErrorShow = (show) => {
        this.setState({errorShow: show})
    }
    onPageSizeSelect = (eventKey, event) => {
        this.setState({pageSize: eventKey})
        this.props.onPageSizeChange(eventKey);
    }

    renderModalAlert() {
        return (
            <Modal
                show={this.state.errorShow}
                onHide={this.closeError}
                size="lg"
                aria-labelledby="contained-modal-title-vcenter"
                centered
            >
                <Modal.Header closeButton>
                    <Modal.Title id="contained-modal-title-vcenter">
                        <div className="">Error</div>
                    </Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <h4>The following errors occurred:</h4>
                    <ul>
                        <li>Start time can not be after end time</li>
                    </ul>
                </Modal.Body>
                <Modal.Footer>
                    <button className="btn btn-error" onClick={this.closeError}>Close</button>
                </Modal.Footer>
            </Modal>
        )
    }

    renderAlert() {
        return (
            <div>
                <Alert variant="danger" show={this.state.errorShow} onClose={this.closeError} dismissible>
                    <Alert.Heading>Oh snap! You got the following error!</Alert.Heading>
                    <ul>
                        <li>Start time can not be after end time</li>
                    </ul>
                </Alert>
            </div>
        )
    }

    renderPageSelect() {
        return (
            <DropdownButton id="dropdown-basic-button" size="m" variant="light" title={this.state.pageSize}>
                {[10,20,50,100].map((pageSize)=>{
                    return this.renderPageSelectItem(pageSize)
                })}
            </DropdownButton>
        );
    }

    renderPageSelectItem(value) {
        if (this.state.pageSize === value) {
            return <Dropdown.Item key={value} eventKey={value} active>{value}</Dropdown.Item>
        }
        return <Dropdown.Item key={value} eventKey={value} onSelect={this.onPageSizeSelect}>{value}</Dropdown.Item>
    }

    render() {
        return (
            <div className="container-data-status">
                <div className="row">
                    <div className="col-6">
                        <div className="container-data-status">
                            <div className="row">
                                <div className="col-5 data-status-text-color">
                                    Start time
                                </div>
                                <div className="col-5 data-status-text-color">
                                    End time
                                </div>
                                <div className="col-2">
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-5">
                                    <Datetime closeOnSelect={true} onChange={this.onStartDateTimeChange}
                                              value={this.state.fromTimestamp}/>
                                </div>
                                <div className="col-5">
                                    <Datetime closeOnSelect={true} onChange={this.onEndDateTimeChange}
                                              value={this.state.toTimestamp}/>
                                </div>
                                <div className="col-2">
                                    <button className="btn btn-primary" onClick={this.onSearchClick}>Search</button>
                                </div>
                            </div>
                        </div>

                    </div>
                    <div className="col-6">
                        <div className="container-data-status">
                            <div className="row">
                                <div className="col-4">

                                </div>
                                <div className="col-4">
                                </div>
                                <div className="col-4 data-status-text-color">
                                    Items Per Page
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-4"></div>
                                <div className="col-4"></div>
                                <div className="col-4">
                                    {this.renderPageSelect()}
                                </div>
                            </div>

                        </div>
                    </div>
                </div>
                <div className="">
                    <div className="container-fluid error-alert">
                        {this.renderAlert()}
                    </div>
                </div>

            </div>
        );
    }


}

export default DeviceDataTableHeader;