import React from 'react';
import {BrowserRouter as Router, Route} from 'react-router-dom';
import SideBar from './sideBar'
import NavBar from './navBar'
import IndexPage from './page/indexPage'
import DeviceListPage from './page/deviceListPage'
import DeviceDataPage from './page/deviceDataPage'
import {Provider} from 'react-redux';
import store from './store';
import Hidden from '@material-ui/core/Hidden';

const routes = (
    <Provider store={store}>
        <Router>
            <div id="wrapper">
                <div className="d-flex flex-column container-fluid" id="content-wrapper">
                    <div className="bg-itm row" id="content">
                        <NavBar/>
                        <Hidden smDown>
                            <div className="col-xl-2 col-lg-2 col-md-2 col-xs-2">
                                <SideBar/>
                            </div>
                        </Hidden>
                        <div className="col-xl-10 col-lg-10 col-md-10 col-xs-10">
                            <Route path='/' exact component={IndexPage}/>
                            <Route path='/deviceList' exact component={DeviceListPage}/>
                            <Route path='/deviceData/:deviceId' component={DeviceDataPage}/>
                        </div>
                    </div>
                </div>
            </div>
        </Router>
    </Provider>

)

export default routes;

