import * as actionTypes from '../actions/actionTypes';

export default function deviceListPageReducer(state = [], action) {
    switch (action.type) {
        case actionTypes.FETCH_All_DEVICE_LIST:
            return action.deviceList;
        case actionTypes.UPDATE_PROFILE:
            return state.map((device) => {
                if (device.device.id !== action.payload.id) {
                    return device;
                }
                return {
                    ...device,
                    device: {...action.payload}
                }
            })
        default:
            return state;
    }
}
