import React, { Component } from 'react';
import Modal from "react-bootstrap/Modal";
import DeviceProfileHeader from "./DeviceProfileHeader";

class DeviceProfileButtons extends Component {
    state = { showCancel: false, showSave: false };

    // Cancel Modal
    setCancelShow = (show) => {
        this.setState({ showCancel: show })
    }
    handleCancelClose = () => this.setCancelShow(false);
    handleCancelShow = () => this.setCancelShow(true);


    // Save Modal
    setSaveShow = (show) => {
        this.setState({ showSave: show })
    }
    handleSaveClose = () => this.setSaveShow(false);
    handleSaveShow = () => this.setSaveShow(true);
    onSaveClick = () => {
        this.handleSaveShow();
    }
    onCancelClick = () => {
        this.handleCancelShow();
    }


    onCancelCancelClick = () => {
        this.handleCancelClose()
    }

    onCancelSaveClick = () => {
        this.handleSaveClose()
    }

    onConfirmCancelClick = () => {
        this.handleCancelClose();
        this.props.onCancelClick();

    }
    onConfirmSaveClick = () => {
        this.handleSaveClose()
        this.props.onSaveClick(this.props.device)
    }

    render() {
        return (
            <div className="row profile-form-buttons mt-3">
                <div className="container-fluid">
                    <div className="row">
                        <div className="col-6 text-center">
                            <button type="button" className="btn btn-outline-primary mb-n1 btn-block"
                                onClick={this.onCancelClick}>Cancel
                            </button>
                        </div>
                        <div className="col-6 text-center">
                            <button type="button" className="btn btn-primary mb-n1 btn-block" onClick={this.onSaveClick}>Save
                            </button>
                        </div>
                    </div>
                </div>
                

                <Modal show={this.state.showCancel} onHide={this.handleCancelClose} backdrop="static"
                    aria-labelledby="contained-modal-title-vcenter"
                    centered>
                    <Modal.Body className="modal-width-edit-profile">
                        <div>Change not save, are you sure you want to leave?</div>
                        <DeviceProfileHeader {...this.props.device} />
                        <div className="container-fluid mt-4">
                            <div className="row">
                                <div className="col-6 text-center">
                                    <button className="btn btn-outline-primary btn-block mb-n1" onClick={this.onCancelCancelClick}>
                                        Cancel
                                    </button>
                                </div>
                                <div className="col-6 text-center">
                                    <button className="btn btn-primary btn-block mb-n1" onClick={this.onConfirmCancelClick}>
                                        Leave
                                    </button>
                                </div>
                            </div>
                        </div>
                    </Modal.Body>
                </Modal>

                <Modal show={this.state.showSave} onHide={this.handleSaveClose} backdrop="static"
                    aria-labelledby="contained-modal-title-vcenter" centered>
                    <Modal.Body className="modal-width-edit-profile">
                        <div>Are you sure you want to make the change?</div>
                        <DeviceProfileHeader {...this.props.device} />
                    </Modal.Body>
                    <Modal.Footer>
                        <div className="container-fluid">
                            <div className="row">
                                <div className="col-6 text-center">
                                    <button className="btn btn-outline-primary btn-block mb-n1" onClick={this.onCancelSaveClick}>
                                        Cancel
                                    </button>
                                </div>
                                <div className="col-6 text-center">
                                    <button className="btn btn-primary btn-block mb-n1" onClick={this.onConfirmSaveClick}>
                                        Save Changes
                                    </button>
                                </div>
                            </div>
                        </div>
                    </Modal.Footer>
                </Modal>
            </div>
        );
    }
}

export default DeviceProfileButtons;
