import { combineReducers } from 'redux'
import status from './dashboardStatusReducer'
import chartDeviceStatus from './chartDeviceStatusReducer'
import device from './deviceListPageReducer'
import deviceData from './deviceDataReducer'
import DeviceDataListReducer from "./DeviceDataListReducer";


export default combineReducers({
    dashboardStatus:status,
    chartDeviceStatus:chartDeviceStatus,
    device:device,
    deviceData:deviceData,
    deviceDataTable: DeviceDataListReducer
});
