
export const computeAuditedPendingErrorData = (statusCounts) => {
    if (statusCounts != null) {
        let auditedData = statusCounts.CLIENT_AUDIT;
        let pendingData = statusCounts.REPLY_MSG_RECEIVE + statusCounts.REPLY_MSG_RECEIVE_TX_COUNT_WARN;
        let errorData = statusCounts.LEDGER_INPUT_ERROR
            + statusCounts.CLIENT_AUDIT_ERROR
            + statusCounts.OBJECTION_SUCCESS
            + statusCounts.OBJECTION_ERROR
            + statusCounts.TX_COUNT_EXCEED;

        let sum = auditedData + pendingData + errorData;

        return ({
            auditedDataPercent: [(auditedData*100)/sum],
            pendingDataPercent: [(pendingData*100)/sum],
            errorDataPercent: [(errorData*100)/sum],
            auditedData: [auditedData],
            pendingData: [pendingData],
            errorData: [errorData]
        })
    }else{
        return({
            auditedData: [0],
            pendingData: [0],
            errorData: [0]
        })
    }
}