function paddingZero(number) {
    if (number < 10) {
        return '0' + number
    }
    return number;
}

export const isArrayEmpty = (array) => {
    return array.length > 0;
}

export function timeStamp2String(unixTime) {
    let datetime = new Date();
    datetime.setTime(unixTime);
    let year = datetime.getFullYear();
    let month = paddingZero(datetime.getMonth() + 1);
    let date = paddingZero(datetime.getDate());
    let hour = paddingZero(datetime.getHours());
    let minute = paddingZero(datetime.getMinutes());
    let second = paddingZero(datetime.getSeconds());
    return year + "-" + month + "-" + date + " " + hour + ":" + minute + ":" + second
};

export function timeStamp2Hour(time) {
    let datetime = new Date();
    datetime.setTime(time);
    let hour = paddingZero(datetime.getHours());
    return hour + ":00" 
}
