import React from "react";
import Chart from 'chart.js' 


const options = {
  tooltips: {
    enabled: true,
    displayColors: true,
    mode: 'nearest',
    intersect: false,
  },
  responsive: true,
  maintainAspectRatio: false,
};



export default class PieChart extends React.Component {
  constructor(props) {
    super(props)
    this.canvasRef = React.createRef();
  }
  componentDidUpdate() {
    this.myChart.data.datasets[0].data = this.props.pieChartData
    this.myChart.update();
  }

  componentDidMount() {
    this.myChart = new Chart(this.canvasRef.current, {
      type: 'pie',
      data: {
        labels: ['SolarPanel', 'Tracker', 'Image', 'Other'],
        datasets: [{
          //預設資料
          data: this.props.pieChartData,
          backgroundColor: [
            //資料顏色
            "#5B9FE1",
            "#BC8AE2",
            "#77C95F",
            "#FF5757"
          ],
        }],
      },
      options: options,
    });
  }


  render() {
    return (
      <canvas ref={this.canvasRef} />
    )
  }
}