import React from "react";
import Chart from 'chart.js' 
import { timeStamp2Hour } from "../util"

//TODO
let options = {
    tooltips: {
        displayColors: true,
        mode:'label'
    },
    scales: {
        xAxes: [{
            stacked: true,
            gridLines: {
                display: false,
            }
        }],
        yAxes: [{
            stacked: true,
            ticks: {
                beginAtZero: true,
                min: 0,
            },
            type: 'linear',
        }]
    },
    responsive: true,
    maintainAspectRatio: false,
    legend: { position: 'bottom' },
};

export default class BarChart extends React.Component {
    constructor(props) {
        super(props)
        this.canvasRef = React.createRef();
    }
    componentDidUpdate() {
        const chartDeviceStatus = this.props.dataSource
        this.chartData = {
            labels: [],
            auditedData: [],
            pendingData: [],
            errorData: []
        }

        chartDeviceStatus.forEach((data) => {
            this.chartData.labels.push(timeStamp2Hour(data.fromTimestamp))
            const statusCounts = data.statusCounts;
            let auditedData = statusCounts.CLIENT_AUDIT;
            let pendingData = statusCounts.REPLY_MSG_RECEIVE + statusCounts.REPLY_MSG_RECEIVE_TX_COUNT_WARN;
            let errorData = statusCounts.LEDGER_INPUT_ERROR
                + statusCounts.CLIENT_AUDIT_ERROR
                + statusCounts.OBJECTION_SUCCESS
                + statusCounts.OBJECTION_ERROR
                + statusCounts.TX_COUNT_EXCEED;
            this.chartData.auditedData.push(auditedData)
            this.chartData.pendingData.push(pendingData)
            this.chartData.errorData.push(errorData)
        });
        this.myChart.data.labels = this.chartData.labels
        this.myChart.data.datasets[0].data = this.chartData.auditedData
        this.myChart.data.datasets[1].data = this.chartData.errorData
        this.myChart.data.datasets[2].data = this.chartData.pendingData
        this.myChart.update();
    }
    componentDidMount() {
        this.myChart = new Chart(this.canvasRef.current, {
            type: 'bar',
            data: {
                labels: [],
                datasets: [{
                    label: 'Audited',
                    backgroundColor: "#77C95F",
                    data: [],
                }, {
                    label: 'Error',
                    backgroundColor: "#FF5757",
                    data: [],
                }, {
                    label: 'Not yet audited',
                    backgroundColor: "#939290",
                    data: [],
                }],
            }, options: options
        });
    }
    render() {
        return (
            <canvas ref={this.canvasRef} />
        )
    }
}