import React from "react";
import DeviceDataTableHeader from "./DeviceDataTableHeader";
import DeviceDataTableBody from "./DeviceDataTableBody";
import DeviceDataTableFooter from "./DeviceDataTableFooter";
import DeviceDataTableXsBody from "./DeviceDataTableXsBody"
import '../../css/DeviceDataTable.css'
import moment from "moment";
import Modal from "react-bootstrap/Modal";
import Button from "react-bootstrap/Button";
import DeviceDataTableSingleInfo from "../singleInfo/DeviceDataTableSingleInfo";
import DeviceDataTableXsSingleInfo from "../singleInfo/DeviceDataTableXsSingleInfo";
import DeviceDataTableVerify from "./DeviceDataTableVerify";
import _ from 'lodash';
import Hidden from '@material-ui/core/Hidden';

class DeviceDataTable extends React.Component {
    state = {
        tableOptions: {
            deviceId: this.props.deviceId,
            fromTimestamp: null,//moment(moment().valueOf() - 43200000),
            page: 0,
            pageSize: 10,
            toTimestamp: null //moment().valueOf()
        },
        pageSize: 10,
        currentPage: 0,
        startClearanceOrder: 0,
        endClearanceOrder: 0,
        deviceIds: [],
        show: false,
        selectedDevice: null,
        verifyShow: false
    }

    onHeaderChange = (options) => {
        this.setState({...this.state.tableOptions, ...options})
    }

    onSearchClick = (options) => {
        let tableOptions = {...this.state.tableOptions, ...options, page: 0}
        this.setState({tableOptions})
        this.props.onSearchClick(tableOptions);
    }

    onPagingClicked = (page) => {
        let tableOptions = {...this.state.tableOptions, page}
        this.setState({tableOptions});
        this.props.onPagingClick(tableOptions);

    }

    onPageSizeChange = (pageSize) => {
        let tableOptions = {...this.state.tableOptions, pageSize, page: 0}
        this.setState({tableOptions});
        this.props.onPageSizeChange(tableOptions);
    }


    renderFooter() {
        if (!this.props.displayFooter) {
            return null;
        }
        let currentPage = this.props.currentPage;
        let totalPageCount = this.props.totalPageCount;
        let displayPageCount = this.props.displayPageCount;
        return <DeviceDataTableFooter currentPage={currentPage}
                                      totalPage={totalPageCount}
                                      displayPageCount={displayPageCount}
                                      onPagingClicked={this.onPagingClicked}/>

    }

    renderHeader() {
        if (!this.props.displayFilter) {
            return null;
        }
        return <DeviceDataTableHeader options={this.state.tableOptions} onChange={this.onHeaderChange}
                                      onSearchClick={this.onSearchClick} onPageSizeChange={this.onPageSizeChange}/>
    }

    deviceNames() {
        if (this.props.deviceDataList) {
            this.props.deviceDataList.map((device) => {
                return device.deviceId;
            })
        }
        return [];
    }
    handleVerifyClose = () => {
        this.setState({verifyShow: false})
    }
    handleClose = () => {
        this.setState({show: false})
    }
    handleShow = (e) => {
        const selectedId = parseInt(e.currentTarget.getAttribute("data-id"));
        const selectedDevice = _.filter(this.props.deviceDataList,(deviceData) =>{
                return parseInt(deviceData.id) === selectedId;
        })[0];
        this.setState({selectedDevice});
        this.setState({show: true});
    }
    handleVerifyShow = (e) => {
        const selectedId = parseInt(e.currentTarget.getAttribute("data-id"));
        const selectedDevice = _.filter(this.props.deviceDataList,(deviceData) =>{
                return parseInt(deviceData.id) === selectedId;
        })[0];
        this.setState({selectedDevice: selectedDevice});
        this.setState({verifyShow: true});
    }

    render() {
        return (
            <div className="deviceDataTable">
                <Hidden smDown>
                    <div className="deviceDataTableHeader">
                        {this.renderHeader()}
                    </div>
                </Hidden>
                <Hidden smDown>
                    <div className="deviceDataTableBody">
                        <DeviceDataTableBody data={this.props.deviceDataList} handleShow={this.handleShow} handleVerifyShow={this.handleVerifyShow}/>
                        <Modal show={this.state.show} onHide={this.handleClose}>
                            <Modal.Body className="bg-itm modal-width">
                                <DeviceDataTableSingleInfo data={this.state.selectedDevice}/>
                                <div className="row">
                                    <div className="col-10">
                                    </div>
                                    <div className="col-2 ml-auto">
                                        <Button variant="primary" onClick={this.handleClose} className="mt-3">
                                            Leave
                                        </Button>
                                    </div>
                                </div>
                            </Modal.Body>
                        </Modal>

                        <Modal show={this.state.verifyShow} onHide={this.handleVerifyClose}>
                            <Modal.Body className="bg-itm">
                                <h4 className="modal-title text-left text-white mb-3" id="exampleModalCenterTitle">
                                    Verification Detail
                                </h4>
                                <DeviceDataTableVerify data={this.state.selectedDevice}/>
                                <div className="row">
                                    <div className="col-8">
                                    </div>
                                    <div className="col-4 ml-auto">
                                        <Button variant="primary" onClick={this.handleVerifyClose} className="mt-3">
                                            Leave
                                        </Button>
                                    </div>
                                </div>
                            </Modal.Body>
                        </Modal>

                    </div>
                    <div className="deviceDataTableFooter">
                        {this.renderFooter()}
                    </div>
                </Hidden>
                <Hidden mdUp>
                    <div className="bg-white card">
                        <div className="card-body">
                            <DeviceDataTableXsBody data={this.props.deviceDataList} handleShow={this.handleShow} handleVerifyShow={this.handleVerifyShow} />
                            <Modal show={this.state.show} onHide={this.handleClose}>
                                <Modal.Body style={{'maxHeight': 'calc(100vh - 160px)', 'overflowY': 'auto'}} className="bg-itm modal-width">
                                    <DeviceDataTableXsSingleInfo data={this.state.selectedDevice} deviceList={this.props.deviceDataList}/>
                                    <div className="row">
                                        <div className="col-9">
                                        </div>
                                        <div className="col-3 ml-auto mr-1">
                                            <Button variant="primary" onClick={this.handleClose} className="mt-3">
                                                Leave
                                            </Button>
                                        </div>
                                    </div>
                                </Modal.Body>
                            </Modal>
                        </div>
                    </div>
                    <div className="deviceDataTableFoote mt-3">
                        {this.renderFooter()}
                    </div>
                </Hidden>

            </div>
        );
    }
}


export default DeviceDataTable;