import React, { Component } from 'react';
import DeviceProfileButtons from "./DeviceProfileButtons";
import * as categoryConstants from '../../common/DeviceCategory';

class DeviceProfileForm extends Component {
    state = { device: { ...this.props.device } }
    onHardwareTypeChange = (event) => {
        const hardwareType = event.target.value;
        this.setState(prevState => ({
            device: {
                ...prevState.device,
                hardwareType
            }
        }))
    }
    onNameChange = (event) => {
        const name = event.target.value;
        this.setState(prevState => ({
            device: {
                ...prevState.device,
                name
            }
        }))
        // this.device.name = event.target.value;
    }

    onCategoryChange = (event) => {
        const category = event.target.value;
        this.setState(prevState => ({
            device: {
                ...prevState.device,
                category
            }
        }))
    }
    onCancelClick = () => {
        this.props.onCancelClick();
    }

    onSaveClick = (device) => {
        this.props.onSaveClick(device)
    }

    onStatusChange = (event)=>{
       const status = event.target.value;
        this.setState(prevState => ({
            device: {
                ...prevState.device,
                status
            }
        }))
    }

    render() {
        const { device } = this.state;
        return (
            <div className="mt-3">
                <h4 className="text-white text-left">Edit Profile</h4>
                <div className="input-group input-group-lg mb-3 mt-3">
                    <div className="input-group-prepend profile-form-input-title">
                        <span
                            className="input-group-text container-fluid align-self-center text-center">Device Name</span>
                    </div>
                    <input type="text" className="form-control" value={device.name || ""}
                        onChange={this.onNameChange} />
                </div>
                <div className="input-group input-group-lg mb-3">
                    <div className="input-group-prepend profile-form-input-title">
                        <span className="input-group-text container-fluid">Category</span>
                    </div>
                    <select value={device.category || ""} className="form-control" onChange={this.onCategoryChange}>
                        <option  value=""></option>
                        <option className="form-select-option" value={categoryConstants.SOLAR_PANEL_METER}>Solar Panel Meter</option>
                        <option value={categoryConstants.TRACKER} >Tracker</option>
                        <option value={categoryConstants.IMAGE_INPUT}>Image</option>
                        <option value={categoryConstants.OTHER}>Other</option>
                    </select>
                    {/*<input type="text" className="form-control" aria-describedby="basic-addon3"/>*/}
                </div>
                <div className="input-group input-group-lg mb-3">
                    <div className="input-group-prepend profile-form-input-title">
                        <span className="input-group-text container-fluid">Hardware Type</span>
                    </div>
                    <input type="text" className="form-control" aria-describedby="basic-addon3"
                        value={device.hardwareType || ""} onChange={this.onHardwareTypeChange} />
                </div>
                <div className="input-group input-group-lg mb-3">
                    <div className="input-group-prepend profile-form-input-title">
                        <span className="input-group-text container-fluid">Status</span>
                    </div>
                    <select value={device.status || ""} className="form-control" onChange={this.onStatusChange}>
                        <option value=""></option>
                        <option value="ONLINE">ON-LINE</option>
                        <option value="OFFLINE">OFF-LINE</option>
                    </select>
                </div>
                <DeviceProfileButtons device={device} onCancelClick={this.onCancelClick} onSaveClick={this.onSaveClick}/>
            </div>

        )
    }
}

export default DeviceProfileForm
