import React from "react";
import '../../css/dashboard.css';
import HorizontalBarChart from "../chart/horizontalBarChart";
import * as categoryConstants from '../../common/DeviceCategory';
import editIcon from '../../image/edit.svg';
import deleteIcon from '../../image/delete.svg'
import {Link} from 'react-router-dom';
import {timeStamp2String} from '../util';
import DeviceProfile from "../DeviceProfile/DeviceProfile";
import Modal from "react-bootstrap/Modal";
import IconButton from '@material-ui/core/IconButton';
import DeviceProfileHeader from "../DeviceProfile/DeviceProfileHeader";

class DeviceCard extends React.Component {
    state = {
        showProfile: false,
        showDelete: false
    };
    handleDeleteClose = () => {
        this.setState({showDelete: false})

    }
    handleDeleteShow = () => {
        this.setState({showDelete: true})
    }

    setShow = (show) => {
        this.setState({showProfile: show})
    }
    handleClose = () => this.setShow(false);
    handleShow = () => this.setShow(true);

    onProfileCancelClicked = () => {
        // this.props.onProfileCancelClicked();
        this.handleClose();
    }

    onProfileSaveClicked = (device) => {
        this.props.onProfileSaveClicked(device);
        this.handleClose();
    }
    onEditProfileClicked = () => {
        this.handleShow();
    }

    renderStatusBarChart() {
        if(!this.props.horizontalBarChartData){
            return null
        }
        function IsBarChartDataEmpty({auditedData, errorData, pendingData}) {
            return (auditedData[0] === 0 && pendingData[0] === 0 && errorData[0] === 0)
        }

        if (IsBarChartDataEmpty(this.props.horizontalBarChartData)) {
            return (
                <div className="card-title p-3">
                    <h6>Status (Last 12 Hours)</h6>
                    <div className="chart-horizontalBar">
                        No data available
                    </div>
                </div>
            )
        }
        return (
            <div className="card-title p-3">
                <h6>Status (Last 12 Hours)</h6>
                <div className="chart-horizontalBar">
                    <HorizontalBarChart {...this.props.horizontalBarChartData} />
                </div>
            </div>
        );
    }

    render() {
        const updateTime = timeStamp2String(this.props.device.updateTime)
        const toPath = `/deviceData/${this.props.device.id}`
        return (
            <>
                <div className="bg-white card">
                    <div className="card-body border-bottom border-dark">
                        <div className="profile-header-card-body border-dark shadow-sm rounded">
                            <div className="text-right">
                                <div className="d-inline">
                                    <IconButton aria-label="delete" onClick={this.handleDeleteShow}>
                                        <img src={deleteIcon} width="18" alt=""/>
                                    </IconButton>
                                </div>
                                <div className="d-inline">
                                    <IconButton aria-label="delete" onClick={this.onEditProfileClicked}>
                                        <img src={editIcon} width="18" alt=""/>
                                    </IconButton>
                                </div>

                            </div>
                            <div className="row">
                                <div className="col-3 align-self-center text-right">
                                    <Link to={toPath} className='btn btn-dark btn-xl btn-circle text-light'>
                                        <img alt="" src={categoryConstants[`ICON_${this.props.device.category}`]} width="40" className="categoryIcon"/>
                                    </Link>
                                </div>
                                <div className="col-9 mt-4">
                                    <h5 className="card-title text-left text-truncate pl-3 pr-3" >
                                        <Link data-toggle="tooltip" title={this.props.device.name} to={toPath}>{this.props.device.name}</Link>
                                    </h5>
                                </div>
                            </div>
                        </div>
                        
                        <Modal show={this.state.showProfile} onHide={this.handleClose}
                                backdrop="static"
                                aria-labelledby="contained-modal-title-vcenter"
                                centered>
                            <Modal.Body className="bg-itm modal-width-edit-profile">
                                <DeviceProfile
                                    device={this.props.device}
                                    onCancelClicked={this.onProfileCancelClicked}
                                    onSaveClicked={this.onProfileSaveClicked}
                                />
                            </Modal.Body>
                        </Modal>
                        <div className="ml-3">
                            <p className="text-dark card-text small">{this.props.device.hardwareType}</p>
                            <p className="text-truncate" data-toggle="tooltip" title={this.props.device.name}><small>ID: {this.props.device.id}</small></p>
                            <p><small>Last Update Time</small></p>
                            <p><small>{updateTime}</small></p>
                        </div>
                    </div>
                    {this.renderStatusBarChart()}
                </div>
                <Modal show={this.state.showDelete} onHide={this.handleDeleteClose} backdrop="static"
                        aria-labelledby="contained-modal-title-vcenter" centered>
                    <Modal.Body className="modal-width-edit-profile">
                        <div>Are you sure you want to delete this device?</div>
                        <DeviceProfileHeader {...this.props.device} />
                    </Modal.Body>
                    <Modal.Footer>
                        <div className="container-fluid">
                            <div className="row">
                                <div className="col-6 text-center">
                                    <button className="btn btn-outline-primary btn-block mb-n1" onClick={this.handleDeleteClose}>
                                        Cancel
                                    </button>
                                </div>
                                <div className="col-6 text-center">
                                    <button className="btn btn-primary btn-block mb-n1">
                                        Delete
                                    </button>
                                </div>
                            </div>
                        </div>
                    </Modal.Footer>
                </Modal>
            </>
        )
    }
}

export default DeviceCard;
