import React, { useRef, useEffect, useState } from 'react';
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faDownload, faInfo, faCheck, faTimes} from "@fortawesome/free-solid-svg-icons";
import { makeStyles } from '@material-ui/core/styles';
import Avatar from '@material-ui/core/Avatar';

const useStyles = makeStyles({
    table: {
      maxWidth: 500,
    },
    btn: {
        width: 100
    },
    green: {
        backgroundColor: "#77C95F"
    },
    red: {
        backgroundColor: "#FF5757"
    }
});
const DeviceDataTableVerify = (props) => {
    const classes = useStyles();
    const renderVerify = (device) => {
        
        if(device.verificationResult === "OK") {
            return (
                <Avatar className={classes.green}>
                    <FontAwesomeIcon icon={faCheck}/>
                </Avatar>
            )
        }else {
            return (
                <Avatar className={classes.red}>
                    <FontAwesomeIcon icon={faTimes}/>
                </Avatar>
            )
        }
    }

    return (
        <>
            <table className="table table-bordered">
                <tbody>
                    <tr>
                        <th className="verify-table-th" scope="row">Result</th>
                        <td className="verify-table-td">
                            {renderVerify(props.data)}
                        </td>
                    </tr>
                    <tr>
                        <th className="verify-table-th" scope="row">Description</th>
                        <td className="verify-table-td">
                            {props.data.verificationResult}
                        </td>
                    </tr>
                    <tr>
                        <th className="verify-table-th" scope="row">Index value</th>
                        <td className="verify-table-td">
                            {props.data.indexValue}
                        </td>
                    </tr>
                    <tr>
                        <th className="verify-table-th" scope="row">Clearance Order</th>
                        <td className="verify-table-td">
                            {props.data.clearanceOrder}
                        </td>
                    </tr>
                    
                </tbody>
            </table>
        </>
    )
}
export default DeviceDataTableVerify;