import React from "react";
import 'jquery';
import '../../css/dashboard.css';
import StatusCard from "../card/statusCard";
import BarChart from "../chart/barChart";
import {
    fetchAllChartDeviceStatus,
    fetchAllDeviceData,
    fetchAllDeviceListPage,
    fetchDashboardStatus,
    GetDeviceData
} from "../../actions/axios"
import {connect} from 'react-redux';
import PieChart from "../chart/pieChart"
import DeviceDataTable from "../DeviceDataTable/DeviceDataTable";
import {Dropdown, DropdownButton} from "react-bootstrap";

const computeDeviceCategory = (device) => {
    let categoryData = [0, 0, 0, 0];
    device.forEach(data => {
        const category = data.device.category
        if (category === "SOLAR_PANEL_METER") {
            categoryData[0] += 1
        } else if (category === "TRACKER") {
            categoryData[1] += 1
        } else if (category === "IMAGE_INPUT") {
            categoryData[2] += 1
        } else {
            categoryData[3] += 1
        }
    })
    categoryData = [(categoryData[0] / device.length) * 100, (categoryData[1] / device.length) * 100, (categoryData[2] / device.length) * 100, (categoryData[3] / device.length) * 100]
    return categoryData;
}

class IndexPage extends React.Component {
    state = {
        dataTableOption: {
            currentPage: 0,
            pageCount: 0,
            pageSize: 10
        },
        barChartOption: {
            fromTimestamp: null,
            toTimestamp: null,
            duration: 3600000,
        }
    };
    onSearchClick = (tableOptions) => {
        this.props.GetDeviceData(tableOptions)
    }
    onPagingClick = (tableOptions) => {
        this.props.GetDeviceData(tableOptions)
    }
    onPageSizeChange = (tableOptions) => {
        this.props.GetDeviceData(tableOptions)
    }

    renderStatusRow() {
        return (
            <div className="row">
                <div className="col-xl-3 col-md-6 mb-4">
                    <StatusCard type="device" title="Total Device Number" content={this.props.status.deviceCount}/>
                </div>
                <div className="col-xl-3 col-md-6 mb-4">
                    <StatusCard type="txCount" title="Tx Count Left" content={this.props.status.txCount}/>
                </div>
                <div className="col-xl-3 col-md-6 mb-4">
                    <StatusCard type="ethereum" title="Ethereum Status" content={this.props.status.ethereumStatus}/>
                </div>
                <div className="col-xl-3 col-md-6 mb-4">
                    <StatusCard type="clearanceOrder" title="Clearance Order"
                                content={this.props.status.clearanceOrder}/>
                </div>
            </div>
        );
    }

    renderChartCard() {
        return (
            <div>
                <div className="card shadow mb-4">
                    <div className="card-body">
                        <div className="col-lg-12">
                            <div className="row">
                                <div className="col-lg-6 col-sm-6 col-md-6">
                                    <h4 className="m-0 text-black">Data Summary</h4>
                                </div>
                                <div className="col-lg-6 col-sm-6 col-md-6 ">
                                    <div className="dropdown text-right">
                                        <DropdownButton id="dropdown-basic-button" size="m" variant="outline-secondary"
                                                        title={"Last 12 hours"}>
                                            <Dropdown.Item key={0} eventKey={0} active>Last 12 hours</Dropdown.Item>
                                        </DropdownButton>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="chart-index-bar mt-3">
                            <BarChart dataSource={this.props.chartDeviceStatus}/>
                        </div>
                    </div>
                </div>
            </div>
        );
    }

    renderPieChart() {
        const pieChartData = computeDeviceCategory(this.props.device)
        const maxPieChartData = Math.max(...pieChartData);
        
        let pieChart;
        if (maxPieChartData > 0) {
            pieChart = <PieChart pieChartData={pieChartData}/>
        } else {
            pieChart = <h1>No Data Display</h1>
        }
        return (
            <div>
                <div className="card shadow mb-4">
                    <div className="card-body">
                        <div className="col-12 text-center mb-4">
                            <div className="row">
                                <h4 className="m-0 text-black">Device Data Distribution by Category</h4>
                            </div>
                        </div>
                        <div className="chart-area justify-content-center align-items-center">
                            {pieChart}
                        </div>
                    </div>
                </div>
            </div>
        );
    }

    componentDidMount() {
        this.props.fetchAllChartDeviceStatus('ALL', this.state.barChartOption.duration, this.state.barChartOption.fromTimestamp, this.state.barChartOption.toTimestamp)
        this.props.fetchAllDeviceData(0, this.state.dataTableOption.pageSize)
        this.props.fetchDashboardStatus();
        this.props.fetchAllDeviceListPage();
        var tableOptions = {
            deviceId: null,
            fromTimestamp: null,
            page: 0,
            pageSize: 10,
            toTimestamp: null
        };
        this.props.GetDeviceData(tableOptions);
    }

    render() {
        return (
            <div className="m-3">
                <div className="d-sm-flex align-items-center justify-content-between mb-4">
                    <h1 className="mb-0 text-white title-mt">Overview</h1>
                </div>
                {this.renderStatusRow()}
                <div className="row">
                    <div className="col-xl-6 col-lg-6 col-md-6">
                        {this.renderChartCard()}
                    </div>
                    <div className="col-xl-6 col-lg-6 col-md-6">
                        {this.renderPieChart()}
                    </div>
                </div>
                <div className="row">
                    <div className="col-xl-12 col-lg-12">
                        <h1 className="mb-2 text-white">Data Status</h1>
                            <DeviceDataTable
                                displayFooter={true}
                                displayFilter={true}
                                onPagingClick={this.onPagingClick}
                                currentPage={this.props.currentPage}
                                deviceDataList={this.props.deviceDataList}
                                totalPageCount={this.props.totalPageCount}
                                displayPageCount={7}
                                totalCount={this.props.totalCount}
                                onSearchClick={this.onSearchClick}
                                onPageSizeChange={this.onPageSizeChange}
                            />
                    </div>
                </div>
            </div>
        )
    }


}

const mapStateToProps = (state) => {
    return {
        device: state.device,
        status: state.dashboardStatus,
        chartDeviceStatus: state.chartDeviceStatus,
        deviceData: state.deviceData,
        deviceDataList: state.deviceDataTable.deviceDataList,
        totalCount: state.deviceDataTable.totalDataCount,
        totalPageCount: state.deviceDataTable.pageCount,
        currentPage: state.deviceDataTable.currentPage
    };
};
export default connect(mapStateToProps, {
    GetDeviceData,
    fetchAllChartDeviceStatus,
    fetchAllDeviceData,
    fetchDashboardStatus,
    fetchAllDeviceListPage
})(IndexPage);