import React, {useState, useEffect} from 'react';
import {timeStamp2String} from '../util'
import * as deviceDataStatus from '../DeviceDataTable/DeviceDataStatus'
import * as categoryConstants from '../../common/DeviceCategory';
import axios from '../../actions/dashboard_https';
import {Link} from 'react-router-dom'
import _ from 'lodash';

const RenderStatus = (props) => {
    const statusObj = deviceDataStatus[props.status];
    const statusCode = statusObj.statusCode;// this.translateStatus(device.status);

    if(statusCode!=="Error"){
        return(
            <>
                <div className="row">
                    <div className="col-xl-2 col-lg-2">
                        <div className={statusObj.statusClass}></div>
                    </div>
                    <div className="col-xl-10 col-lg-10">
                        <span className="ml-2">{statusObj.description}</span>
                    </div>
                </div>
            </>
        ) 
    }
    
}

const DeviceDataTableSingleInfo = (props) => {
    const [data, setData] = useState([]);
    const [deviceId, setDeviceId] = useState("");
    const [clearanceOrder, setClearanceOrder] = useState(0);
    const [indexValue, setIndexValue] = useState(0)
    useEffect(() => {
        if(deviceId) {
            axios.get(`/device/data/status/${deviceId}/${clearanceOrder}/${indexValue}`).then(res => {
                setData(res.data)
            })
        }
    }, [deviceId, clearanceOrder, indexValue])

    useEffect(() => {
        setDeviceId(props.data.deviceId);
        setClearanceOrder(props.data.clearanceOrder);
        setIndexValue(props.data.indexValue)
    }, [])

    return(
        <>
            <div className="bg-white card container-fluid">
                <div className="card-body border-dark">
                    <div className="row">
                        <div className="col-xl-4 col-lg-4 align-self-center text-right">
                            <Link to="#" className='btn btn-dark btn-xl btn-circle text-light'>
                                <img alt="" src={categoryConstants[`ICON_${data.category}`]} width="40" className="categoryIcon"/>
                            </Link>
                        </div>
                        <div className="col-xl-8 col-lg-8 mt-4">
                            <h5 className="card-title">
                                <div className="text-left">ID : {data.deviceId}</div>
                            </h5>
                            <p>Name : {data.deviceName}</p>
                        </div>
                    </div>
                    
                </div>
            </div>
            <div className="row">
                <div className="col-xl-6 col-lg-6 mt-3">
                    <div className="bg-white card container-fluid">
                        <div className="card-body border-dark">
                            <div className="row">
                                <div className="col-xl-12 col-lg-12 align-self-center text-left">
                                    <h5 className="card-title">
                                        Time
                                    </h5>
                                    <p>
                                        {data.timestamp > 0 && timeStamp2String(data.timestamp)}
                                    </p>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="col-xl-6 col-lg-6 mt-3">
                    <div className="bg-white card container-fluid">
                        <div className="card-body border-dark">
                            <div className="row">
                                <div className="col-xl-12 col-lg-12 align-self-center text-left">
                                    <h5 className="card-title">
                                        Status
                                    </h5>
                                    <div>
                                        {data.status != null && 
                                            <RenderStatus status={data.status}/>}
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            <div className="row">
                <div className="col-xl-6 col-lg-6 mt-3">
                    <div className="bg-white card container-fluid single-info-cmd-height">
                        <div className="card-body border-dark">
                            <div className="row">
                                <div className="col-xl-12 col-lg-12 align-self-center text-left">
                                    <h5 className="card-title">
                                        CMD
                                    </h5>
                                    <p>
                                        {data.cmd}
                                    </p>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="col-xl-6 col-lg-6 mt-3">
                    <div className="bg-white card container-fluid">
                        <div className="card-body border-dark">
                            <div className="row">
                                <div className="col-xl-12 col-lg-12 align-self-center text-left">
                                    <h5 className="card-title">
                                        EventLogs
                                    </h5>
                                    <div>
                                        <div className="row">
                                            <div className="col-xl-2 col-lg-2">
                                                <div className="btn btn-pending btn-circle btn-sm"></div>
                                                <br />
                                                <div className="line mt-2 mb-1 ml-line"></div>

                                            </div>
                                            <div className="col-xl-10 col-lg-10">
                                                {_.keys(data.eventLogs)[0]}<br />
                                                {
                                                    _.map(data.eventLogs)[1] > 0 &&
                                                    timeStamp2String(_.map(data.eventLogs)[0])}
                                            </div>
                                        </div>
                                        <div className="row">
                                            <div className="col-xl-2 col-lg-2">
                                                <div className="btn btn-audit btn-circle btn-sm"></div>
                                            </div>
                                            <div className="col-xl-10 col-lg-10">
                                                {_.keys(data.eventLogs)[1]}<br />
                                                {
                                                    _.map(data.eventLogs)[1] > 0 &&
                                                    timeStamp2String(_.map(data.eventLogs)[1])
                                                }
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}
export default DeviceDataTableSingleInfo;