import React, { useState, useEffect } from 'react';
import {Link} from 'react-router-dom';
import axios from '../../actions/dashboard_https';
import {timeStamp2String} from '../util'
import * as deviceDataStatus from '../DeviceDataTable/DeviceDataStatus';
import * as categoryConstants from '../../common/DeviceCategory';
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faDownload} from "@fortawesome/free-solid-svg-icons";
import ethereum from "../../image/ethereum.svg";
import _ from 'lodash';
import DeviceDataTableVerify from "../DeviceDataTable/DeviceDataTableVerify";
import Modal from "react-bootstrap/Modal";
import {faCheck, faTimes} from "@fortawesome/free-solid-svg-icons";
import Button from "react-bootstrap/Button";

const RenderOnChainProof = (props) => {
    const statusObj = deviceDataStatus[props.data.status];
    return (
        <span className={statusObj.btnEthereumClass}>
            <a className="btn btn-dark btn-circle" href={`/etherscan/${props.data.clearanceOrder}`} role="button">
                <img alt="eth" width="15" src={ethereum}/>
            </a>
        </span>
    );
}

const RenderOffChainProof = (props) => {
    const statusObj = deviceDataStatus[props.data.status];
    return (
        <span className={statusObj.btnEthereumClass}>
            <a
                className="btn btn-dark btn-circle"
                href={`/verificationProof/${props.data.clearanceOrder}/${props.data.indexValue}`}
                role="button"><FontAwesomeIcon
                icon={faDownload}/>
            </a>
        </span>
    );
}

const RenderStatus = (props) => {
    const statusObj = deviceDataStatus[props.status];
    const statusCode = statusObj.statusCode;

    if(statusCode!=="Error"){
        return(
            <>
                <div className="row">
                    <div className="col-2">
                        <div className={statusObj.statusClass}></div>
                    </div>
                    <div className="col-10">
                        <span className="ml-2">{statusObj.description}</span>
                    </div>
                </div>
            </>
        ) 
    }
    
}

const DeviceDataTableXsSingleInfo = (props) =>  {
    const [data, setData] = useState([]);
    const [deviceId, setDeviceId] = useState("");
    const [clearanceOrder, setClearanceOrder] = useState(0);
    const [indexValue, setIndexValue] = useState(0);
    const [verifyShow, setverifyShow] = useState(false);
    // const [selectedDevice, setselectedDevice] = useState(null)

    const handleVerifyClose = () => {
        setverifyShow(false);
    }

    const handleVerifyShow = () => {
        // const selectedId = parseInt(e.currentTarget.getAttribute("data-id"));
        // const selectedDevice = _.filter(this.props.deviceDataList,(deviceData) =>{
        //         return parseInt(deviceData.id) === selectedId;
        // })[0];
        // setselectedDevice(selectedDevice);
        setverifyShow(true);
    }

    useEffect(() => {
        if(deviceId) {
            axios.get(`/device/data/status/${deviceId}/${clearanceOrder}/${indexValue}`).then(res => {
                setData(res.data)
            })
       }
    }, [deviceId, clearanceOrder, indexValue])

    useEffect(() => {
        setDeviceId(props.data.deviceId);
        setClearanceOrder(props.data.clearanceOrder);
        setIndexValue(props.data.indexValue);
    }, [])
    const renderVerify = () => {
        const statusObj = deviceDataStatus[props.data.status];
        if(props.data.verificationResult === "OK") {
            return (
                <>
                    <span className={statusObj.btnEthereumClass}>
                        <button className="btn btn-green btn-circle" data-id={props.data.id} onClick={handleVerifyShow}>
                            <FontAwesomeIcon icon={faCheck}/>
                        </button>
                    </span>
                    
                </>
            )
        }else {
            return (
                <>
                    <span className={statusObj.btnEthereumClass}>
                        <button className="btn btn-red btn-circle" data-id={props.data.id} onClick={handleVerifyShow}>
                            <FontAwesomeIcon icon={faTimes}/>
                        </button>
                    </span>
                </>
            )
        }
    }
    return (
        <>
        <div className="bg-white card container-fluid">
            <div className="custom-card-body border-dark">
                <div className="row">
                    <div className="col-3 align-self-center text-right">
                        <Link to="#" className='btn btn-dark btn-xs btn-circle text-light'>
                            <img alt="" src={categoryConstants[`ICON_${data.category}`]} width="25" className="categoryIcon"/>
                        </Link>
                    </div>
                    <div className="col-9 mt-4">
                        <h5 className="card-title">
                            {data.deviceId}
                        </h5>
                        <p>Name : {data.deviceName}</p>
                    </div>
                </div>
            </div>
        </div>
        <div className="bg-white card container-fluid mt-3">
            <div className="custom-card-body border-dark">
                <h5>Time</h5>
                <p>{data.timestamp > 0 && timeStamp2String(data.timestamp)}</p>
            </div>
        </div>
        <div className="bg-white card container-fluid mt-3">
            <div className="custom-card-body border-dark">
                <h5>CMD</h5>
                <p>{data.cmd}</p>
            </div>
        </div>
        <div className="bg-white card container-fluid mt-3">
            <div className="custom-card-body border-dark">
                <h5>Status</h5>
                <div>{data.status != null && <RenderStatus status={data.status}/>}</div>
            </div>
        </div>
            <div className="row">
                <div className="col-6">
                    <div className="bg-white card container-fluid mt-3 xs-single-info-co-height">
                        <div className="custom-card-body border-dark">
                            <h5>CO</h5>
                            <p>{data.clearanceOrder}</p>
                        </div>
                    </div>
                </div>
                <div className="col-6">
                    <div className="bg-white card container-fluid mt-3 xs-single-info-co-height">
                        <div className="custom-card-body border-dark">
                            <h5>Verification</h5>
                            <div>
                                {data.status!=null && renderVerify()}
                                <Modal show={verifyShow} onHide={handleVerifyClose}>
                                    <Modal.Body className="bg-itm">
                                        <h4 className="modal-title text-left text-white mb-3" id="exampleModalCenterTitle">
                                            Verification Detail
                                        </h4>
                                        <DeviceDataTableVerify data={props.data}/>
                                        <div className="row">
                                            <div className="col-8">
                                            </div>
                                            <div className="col-4 ml-auto">
                                                <Button variant="primary" onClick={handleVerifyClose} className="mt-3">
                                                    Leave
                                                </Button>
                                            </div>
                                        </div>
                                    </Modal.Body>
                                </Modal>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div className="row">
                <div className="col-6">
                    <div className="bg-white card container-fluid mt-3">
                        <div className="custom-card-body border-dark">
                        <div className="modal-title-font-size">On-Chain Proof</div>
                            <div className="xs-single-info-proof-height">{data.status != null && <RenderOnChainProof data={data}/>}</div>
                        </div>
                    </div>
                </div>
                <div className="col-6">
                    <div className="bg-white card container-fluid mt-3">
                        <div className="custom-card-body border-dark">
                            <div className="modal-title-font-size">Off-Chain Proof</div>
                            <div className="xs-single-info-proof-height">{data.status != null && <RenderOffChainProof data={data}/>}</div>
                        </div>
                    </div>
                </div>
            </div>
            <div className="bg-white card container-fluid mt-3">
                <div className="custom-card-body border-dark">
                    <h5>EventLogs</h5>
                    <div>
                        <div className="row">
                            <div className="col-2">
                                <div className="btn btn-pending btn-circle btn-sm"></div>
                                <br />
                                <div className="line mt-2 mb-1 ml-line"></div>

                            </div>
                            <div className="col-10">
                                {_.keys(data.eventLogs)[0]}<br />
                                {timeStamp2String(_.map(data.eventLogs)[0])}
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-2">
                                <div className="btn btn-audit btn-circle btn-sm"></div>
                            </div>
                            <div className="col-10">
                                {_.keys(data.eventLogs)[1]}<br />
                                {
                                    _.map(data.eventLogs)[1] > 0 &&
                                    timeStamp2String(_.map(data.eventLogs)[1])
                                }
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}
export default DeviceDataTableXsSingleInfo;
