import React, {Component} from 'react';
import Hidden from '@material-ui/core/Hidden';

class DeviceDataTableFooter extends Component {

    onPagingClicked = (e) => {
        this.props.onPagingClicked(parseInt(e.target.value) - 1)
    }

    renderPagingButtons(key, text, value, enabled) {
        let btn_className = `btn ${(parseInt(this.props.currentPage) + 1 === parseInt(value)) ? "btn-primary" : "btn-outline-primary"} page-rounded-circle btn-block`
        return (
            <li className="page-item ml-2" key={key}>
                <button className={btn_className} aria-label="Previous"
                        onClick={this.onPagingClicked} disabled={!enabled} value={value}>
                    {text}
                </button>
            </li>
        )
    }

    renderPageFooters() {
        if (this.props.currentPage === undefined || this.props.totalPage === 0) {
            return null;
        }
        let pageNum = parseInt(this.props.currentPage) + 1;
        let pages = Math.min(this.props.totalPage,this.props.displayPageCount)// ? parseInt(this.props.displayPageCount);
        let totalPages = parseInt(this.props.totalPage);
        let bufferPages = Math.floor(pages / 2);
        let fromPage = 1;
        // Fist batch paging icons
        if (pageNum > bufferPages + 1) {
            fromPage = pageNum - bufferPages;
        }

        // Last batch paging icons
        if (totalPages - pageNum < bufferPages) {
            fromPage = totalPages - pages + 1;
        }


        let toPage = fromPage + pages;
        let pagingRenderer = [];
        pagingRenderer.push(this.renderPagingButtons("first", "<<", 1, (pageNum > 1)))
        pagingRenderer.push(this.renderPagingButtons("prev", "<", pageNum - 1, (pageNum > 1)))
        for (let number = fromPage; number < toPage; number++) {
            pagingRenderer.push(this.renderPagingButtons(number, number, number, true))
        }
        pagingRenderer.push(this.renderPagingButtons("next", ">", pageNum + 1, (pageNum < totalPages)));
        pagingRenderer.push(this.renderPagingButtons("last", ">>", totalPages, (pageNum < totalPages)));
        return pagingRenderer;
    }

    renderXsPageFooters() {
        if (this.props.currentPage === undefined || this.props.totalPage === 0) {
            return null;
        }
        let pageNum = parseInt(this.props.currentPage) + 1;
        let pages = Math.min(this.props.totalPage, 3)// ? parseInt(this.props.displayPageCount);
        let totalPages = parseInt(this.props.totalPage);
        let bufferPages = Math.floor(pages / 2);
        let fromPage = 1;
        // Fist batch paging icons
        if (pageNum > bufferPages + 1) {
            fromPage = pageNum - bufferPages;
        }

        // Last batch paging icons
        if (totalPages - pageNum < bufferPages) {
            fromPage = totalPages - pages + 1;
        }


        let toPage = fromPage + pages;
        let pagingRenderer = [];
        pagingRenderer.push(this.renderPagingButtons("first", "<<", 1, (pageNum > 1)))
        pagingRenderer.push(this.renderPagingButtons("prev", "<", pageNum - 1, (pageNum > 1)))
        for (let number = fromPage; number < toPage; number++) {
            pagingRenderer.push(this.renderPagingButtons(number, number, number, true))
        }
        pagingRenderer.push(this.renderPagingButtons("next", ">", pageNum + 1, (pageNum < totalPages)));
        pagingRenderer.push(this.renderPagingButtons("last", ">>", totalPages, (pageNum < totalPages)));
        return pagingRenderer;
    }

    render() {
        return (
            <>
                <Hidden smDown>
                    <div className="row justify-content-center align-items-center">
                        <div className="col-3">
                            <nav aria-label="Page navigation">
                                <ul className="pagination">

                                    {this.renderPageFooters()}
                                </ul>
                            </nav>
                        </div>
                    </div>
                </Hidden>
                <Hidden mdUp>
                    <div className="justify-content-center align-items-center">
                        <div className="">
                            <nav aria-label="Page navigation">
                                <ul className="pagination">
                                    {this.renderXsPageFooters()}
                                </ul>
                            </nav>
                        </div>
                    </div>
                </Hidden>
            </>
        );
    }
}

export default DeviceDataTableFooter;