import React, {Component} from 'react';
import '../../css/dashboard.css';
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faDownload, faInfo, faCheck, faTimes} from "@fortawesome/free-solid-svg-icons";
import ethereum from "../../image/ethereum.svg";
import {timeStamp2String} from "../util";
import {OverlayTrigger, Tooltip} from "react-bootstrap";
import * as deviceDataStatus from './DeviceDataStatus';
import Link from '@material-ui/core/Link';

class DeviceDataTableBody extends Component {
    renderHeader() {
        return (
            <tr className="tr-only-hide">
                <th scope="col" className='w-20'>Device ID</th>
                <th scope="col">Time</th>
                <th scope="col">CMD</th>
                <th scope="col">CO</th>
                <th scope="col">Status</th>
                <th scope="col">On-Chain Proof</th>
                <th scope="col">Off-Chain Proof</th>
                <th scope="col">Verification</th>
            </tr>
        )
    }
    renderRows() {
        if (!this.props.data) {
            return null;
        }
        return this.props.data.map((device) => {
            return (
                <tr key={device.id}>
                    <td data-th="Device ID" className='text-left'><Link href="#" onClick={this.props.handleShow} data-id={device.id}>{device.deviceId}</Link></td>
                    <td data-th="Time">{timeStamp2String(device.timestamp)}</td>
                    <td data-th="CMD">
                        {this.rendercmd(device)}
                    </td>
                    <td data-th="CO"><span>{device.clearanceOrder}</span></td>
                    <td data-th="Status">
                        {this.renderStatus(device)}
                    </td>
                    <td data-th="On-Chain Proof">
                        {this.renderOnChainProof(device)}
                    </td>
                    <td data-th="Off-Chain Proof">
                        {this.renderOffChainProof(device)}
                    </td>
                    <td data-th="Verification">
                        {this.renderVerify(device)}
                    </td>
                </tr>
            )

        })
    }
    renderVerify(device) {
        const statusObj = deviceDataStatus[device.status];
        if(device.verificationResult === "OK") {
            return (
                <span className={statusObj.btnEthereumClass}>
                    <button className="btn btn-green btn-circle" data-id={device.id} onClick={this.props.handleVerifyShow}>
                        <FontAwesomeIcon icon={faCheck}/>
                    </button>
                </span>
            )
        }else {
            return (
                <span className={statusObj.btnEthereumClass}>
                    <button className="btn btn-red btn-circle" data-id={device.id} onClick={this.props.handleVerifyShow}>
                        <FontAwesomeIcon icon={faTimes}/>
                    </button>
                </span>
            )
        }
    }
    renderStatus(device) {
        const statusObj = deviceDataStatus[device.status];
        const statusCode = statusObj.statusCode;// this.translateStatus(device.status);

        if(statusCode!=="Error"){
           return <div className={statusObj.statusClass}> </div>
        }
        return (
            <OverlayTrigger key={device.id} placement={'right'} overlay={
                <Tooltip id={`tooltip-${'right'}`}>
                    {statusObj.description}
                </Tooltip>
            }>
                <div className={statusObj.statusClass}> </div>
            </OverlayTrigger>
        )
    }

    renderOffChainProof(device) {
        const statusObj = deviceDataStatus[device.status];
        return (
            <span className={statusObj.btnEthereumClass}>
                <a
                    className="btn btn-dark btn-circle"
                    href={`/verificationProof/${device.clearanceOrder}/${device.indexValue}`}
                    role="button"><FontAwesomeIcon
                    icon={faDownload}/>
                </a>
            </span>
        );
    }

    renderOnChainProof(device) {
        const statusObj = deviceDataStatus[device.status];
        return (
            <span className={statusObj.btnEthereumClass}>
                <a className="btn btn-dark btn-circle" href={`/etherscan/${device.clearanceOrder}`} role="button">
                    <img alt="eth" width="15" src={ethereum}/>
                </a>
            </span>
        );
    }

    rendercmd(device) {
        return (
            <OverlayTrigger key={device.id} placement={'right'} overlay={
                <Tooltip id={`tooltip-${'right'}`}>
                    {device.cmd}
                </Tooltip>
            }>
                <button className="btn btn-dark btn-circle" data-id={device.id} onClick={this.props.handleShow}>
                    <FontAwesomeIcon icon={faInfo}/>
                </button>
            </OverlayTrigger>
        )

    }

    render() {
        return (
            <div>
                <table className="table table-striped table-light table-rwd">
                    <thead>
                        {this.renderHeader()}
                    </thead>
                    <tbody>
                        {this.renderRows()}
                    </tbody>
                </table>
            </div>
        );
    }
}

export default DeviceDataTableBody;