import * as actionTypes from './actionTypes'
import axios from "./dashboard_https";
import moment from "moment";
import * as columnNames from "../component/DeviceDataTable/ColumnNames"
import * as operators from "../component/DeviceDataTable/QueryOperators"

export const fetchDashboardStatus = () => {
    return async (dispatch) => {
        const response = await axios.get('/status');
        dispatch({
            type: actionTypes.FETCH_DASHBOARD_STATUS,
            status: response.data
        })
    };
}

export const fetchAllDeviceListPage = () => {
    return async (dispatch) => {
        const response = await axios.get('/device/profiles')
        dispatch({
            type: actionTypes.FETCH_All_DEVICE_LIST,
            deviceList: response.data
        })
    };
}

export const fetchAllDeviceData = (page, pageSize = 10) => {
    return async (dispatch) => {
        const response = await axios.post('/device/data/homeDataPage', {page: page, pageSize: pageSize});
        dispatch({
            type: actionTypes.FETCH_ALL_DEVICE_DATA,
            deviceData: response.data
        })
    };
};

export const fetchDeviceDataByDeviceId = (deviceId, page = 0, pageSize = 10, fromTimestamp, toTimestamp) => {
    if (!deviceId) {
        return null;
    }
    const timestamp = Math.round((new Date()).getTime());
    fromTimestamp = fromTimestamp || timestamp - 43200000;
    toTimestamp = toTimestamp || timestamp;
    const postData = {
        deviceId: deviceId,
        page: page,
        pageSize: pageSize,
        fromTimestamp: fromTimestamp,
        toTimestamp: toTimestamp
    };
    return async (dispatch) => {
        const response = await axios.post('/device/data/deviceDataPage', postData)
        dispatch({
            type: actionTypes.FETCH_ALL_DEVICE_DATA,
            deviceData: response.data
        })
    };
}

export const fetchAllChartDeviceStatus = (deviceId, duration = 3600000, fromTimestamp, toTimestamp) => {
    if (!deviceId) {
        return null;
    }
    const m = moment();
    const roundUp = m.minute() || m.second() || m.millisecond() ? m.add(1, 'hour').startOf('hour') : m.startOf('hour');
    const timestamp = roundUp.format('x')
    fromTimestamp = fromTimestamp || timestamp - 43200000;
    toTimestamp = toTimestamp || timestamp;
    duration = duration || duration;
    const postData = {
        deviceId: deviceId,
        duration: duration,
        fromTimestamp: fromTimestamp,
        toTimestamp: toTimestamp
    };
    return async (dispatch) => {
        const response = await axios.post('/chart/deviceStatus', postData)
        dispatch({
            type: actionTypes.FETCH_ALL_CHART_DEVICE_STATUS,
            deviceStatus: response.data
        })
    };
};

export const fetchDeviceChart = (deviceId) => {
    return async dispatch => {
        const response = await axios.get('/chart');
        dispatch({
            type: actionTypes.FETCH_ALL_CHART_DEVICE_STATUS,
            deviceStatus: response.data
        })
    }
}


export const UpdateProfile = (device) => {
    let updatedDevice = {
        category: device.category,
        deviceId: device.id,
        deviceName: device.name,
        hardwareType: device.hardwareType,
        status: device.status
    }

    return async dispatch => {
        await axios.post('/device/profile/edit', updatedDevice);
        dispatch({
            type: actionTypes.UPDATE_PROFILE,
            payload: device
        })
    }
}

export const GetDeviceData = (tableOptions) => {
    // const timestamp = Math.round((new Date()).getTime());
    let filterOptions = {
        "filterOptions": [

        ],
        "page": tableOptions.page || 0,
        "pageSize": tableOptions.pageSize || 10
    }
    if (tableOptions.fromTimestamp && tableOptions.toTimestamp) {

        const fromTimestamp = tableOptions.fromTimestamp.valueOf();
        const toTimestamp = tableOptions.toTimestamp.valueOf();
        filterOptions.filterOptions.push({
                "columnName": columnNames.TIMESTAMP,
                "operator": operators.BETWEEN,
                "value1": fromTimestamp.toString(),
                "value2": toTimestamp.toString()
            })
    }

    if (tableOptions.deviceId) {
        filterOptions.filterOptions.push({
            "columnName": columnNames.DEVICEID,
            "operator": operators.EQUAL,
            "value1": tableOptions.deviceId,
            "value2": null
        })
    }

    let url = '/device/data/deviceDataPage';
    return async dispatch => {
        const response = await axios.post(url, filterOptions);
        dispatch({
            type: actionTypes.FETCH_DEVICE_TABLE_DATA,
            payload: {...response.data, currentPage: tableOptions.page}
        })
    }
}